  public get cr_total(): number {

    let sum = 0;
    this.pos_check_settlements.forEach(element => {
      if (element.allowance_ind) {
        sum -= +element.amount;
      }
      else {
        sum += +element.amount;
      }
    });
    return sum;
  }

