import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { catchError, map, tap } from 'rxjs/operators';
import { environment } from '../../environments/environment';
import { Kitchen } from 'projects/reports/src/app/models/kitchen.model';
import { throwError } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class KitchenService {

  constructor(private http: HttpClient) {
  }

  getPosKitchens() {

    return this.http.get<any>(environment.APIENDPOINT + environment.KITCHEN).pipe(
      catchError(errorRes => {
        console.log(errorRes);
        return throwError(() => new Error(errorRes.message || "An error occurred"));
      }),
      tap(response => {
        let kitchenArray: any = [];

        response["pos_kitchens"].forEach(kitchen => {
          let objKitchen = {};
          objKitchen["id"] = kitchen.id;
          objKitchen["code"] = kitchen.code;
          objKitchen["description"] = kitchen.pos_kitchen_translations && kitchen.pos_kitchen_translations.length > 0 ? kitchen.pos_kitchen_translations[0]["description"] : "";
          objKitchen["kitchen_printer1"] = kitchen.kitchen_printer1;
          objKitchen["kitchen_printer2"] = kitchen.kitchen_printer2;
          objKitchen["kitchen_printer3"] = kitchen.kitchen_printer3;
          objKitchen["kitchen_printer4"] = kitchen.kitchen_printer4;
          objKitchen["kitchen_printer5"] = kitchen.kitchen_printer5;
          objKitchen["freeze_ind"] = kitchen.freeze_ind;
          objKitchen["freeze_at"] = kitchen.freeze_at;
          kitchenArray.push(objKitchen);
        });
        return kitchenArray;
      })
    )
  }
}
