  sendFileToServerPDF(fileContent: string, printerName, kotNumber: string) {
    const url = environment.KOT_PRINTER_PDF_ENDPOINT;
    const body = {
      PrinterName: printerName,
      Base64Str: fileContent,
      Filename: `${kotNumber}.txt`
    };

    const headers = new HttpHeaders({
      'Content-Type': 'application/json',
      'Accept': 'application/json'
    });

    this.http.post(url, body, { headers }).pipe(
      catchError(error => {
        console.error('Print request failed:', error);
        return throwError(() => error);
      })
    ).subscribe(response => {
      console.log('Print request successful:', response);
    });

  }


  sendFileToServer1(fileContent: string) {
    const url = environment.KOT_PRINTER_SERVICE_ENDPOINT + environment.SERVER_PRINT;

    const body = {
      fileContent: fileContent,
    };


    this.http.post(url, body).pipe(
      catchError(error => {
        console.error('Print request failed:', error);
        return throwError(() => error);
      })
    ).subscribe(response => {
      console.log('Print request successful');
    });

  }
